import { useState } from "react";
import { signIn } from "next-auth/react";

export default function LoginPage() {
  const [loginMethod, setLoginMethod] = useState("email");

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-r from-blue-100 via-white to-blue-100 px-4">
      <div className="bg-white p-8 rounded-2xl shadow-lg w-full max-w-md">
        <h2 className="text-2xl font-bold mb-6 text-center text-blue-900">
          Login to Your Account
        </h2>

        {/* Toggle Button Group */}
        <div className="flex items-center justify-center mb-6">
          <div className="bg-blue-300 p-1 rounded-full flex space-x-1 shadow-inner">
            <button
              onClick={() => setLoginMethod("email")}
              className={`px-6 py-2 rounded-full text-sm font-medium transition-all duration-300 ${
                loginMethod === "email"
                  ? "bg-blue-600 text-white shadow-md"
                  : "text-gray-700"
              }`}
            >
              Login with Email
            </button>
            <button
              onClick={() => setLoginMethod("mobile")}
              className={`px-6 py-2 rounded-full text-sm font-medium transition-all duration-300 ${
                loginMethod === "mobile"
                  ? "bg-blue-600 text-white shadow-md"
                  : "text-gray-700"
              }`}
            >
              Login with Mobile
            </button>
          </div>
        </div>

        {/* Email Login Form */}
        {loginMethod === "email" && (
          <form className="space-y-4 mb-6">
            <input
              type="email"
              placeholder="Enter your email"
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
            <input
              type="password"
              placeholder="Enter your password"
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
            <button
              type="submit"
              className="w-full bg-blue-600 text-white py-3 rounded-lg font-semibold hover:bg-blue-700 transition duration-200"
            >
              Login with Email
            </button>
          </form>
        )}

        {/* Mobile Login Form */}
        {loginMethod === "mobile" && (
          <form className="space-y-4 mb-6">
            <input
              type="tel"
              placeholder="Enter your mobile number"
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
            <button
              type="submit"
              className="w-full bg-green-600 text-white py-3 rounded-lg font-semibold hover:bg-green-700 transition duration-200"
            >
              Login with Mobile Number
            </button>
          </form>
        )}

        <div className="text-center text-gray-500 mb-4">OR</div>

        {/* Social Login Buttons */}
        <div className="space-y-3 mb-6">
          {/* Google Login */}
          <button
            onClick={() => signIn("google")}
            className="w-full flex items-center justify-center gap-2 border border-gray-300 py-3 rounded-lg hover:bg-gray-100 transition"
          >
            <svg
              className="w-5 h-5"
              viewBox="0 0 48 48"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill="#EA4335"
                d="M24 9.5c3.24 0 5.92 1.12 7.96 2.96l5.9-5.9C33.58 2.76 29.13 1 24 1 14.94 1 7.5 6.87 4.74 14.44l6.89 5.35C13.07 13.2 18.1 9.5 24 9.5z"
              />
              <path
                fill="#34A853"
                d="M24 46c5.13 0 9.58-1.76 13.24-4.96l-6.34-5.18c-1.98 1.33-4.5 2.14-6.9 2.14-5.9 0-10.93-3.7-12.37-8.84L4.74 33.56C7.5 41.13 14.94 47 24 47z"
              />
              <path
                fill="#4A90E2"
                d="M47.5 24c0-1.6-.14-3.13-.4-4.6H24v9.12h13.18c-.56 3.1-2.3 5.74-4.72 7.54l6.34 5.18C43.6 37.1 47.5 31.2 47.5 24z"
              />
              <path
                fill="#FBBC05"
                d="M11.63 28.16A14.96 14.96 0 0 1 9.5 24c0-1.45.21-2.86.59-4.16l-6.89-5.35A22.9 22.9 0 0 0 1 24c0 3.74.89 7.26 2.47 10.44l6.89-5.35z"
              />
            </svg>
            <span className="text-sm font-medium text-gray-700">
              Login with Google
            </span>
          </button>

          {/* LinkedIn Login */}
          <button
            onClick={() => signIn("linkedin")}
            className="w-full flex items-center justify-center gap-2 bg-blue-700 text-white py-3 rounded-lg hover:bg-blue-800 transition"
          >
            <svg
              className="w-5 h-5"
              viewBox="0 0 24 24"
              fill="white"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M19 0h-14C2.24 0 0 2.24 0 5v14c0 2.76 2.24 5 5 5h14c2.76 0 5-2.24 5-5V5c0-2.76-2.24-5-5-5zM8 19H5V9h3v10zM6.5 7.5c-.83 0-1.5-.68-1.5-1.5S5.67 4.5 6.5 4.5 8 5.18 8 6s-.67 1.5-1.5 1.5zM20 19h-3v-5c0-1.1-.9-2-2-2s-2 .9-2 2v5h-3V9h3v1.41C14.33 9.53 15.6 9 17 9c2.21 0 4 1.79 4 4v6z" />
            </svg>
            <span className="text-sm font-medium">Login with LinkedIn</span>
          </button>
        </div>

        {/* Register */}
        <p className="text-sm text-center text-gray-600">
          Donâ€™t have an account?{" "}
          <a href="/register" className="text-blue-600 hover:underline">
            Register here
          </a>
        </p>
      </div>
    </div>
  );
}
